<template>
	<div class="special">
		<!-- <uniSearchBar ref="uniSearchBar" @confirm="search"></uniSearchBar> -->
		<div class="weui-search-bar" id="searchBar" :class="{'weui-search-bar_focusing': isTyping}">
			<form class="weui-search-bar__form">
				<div class="weui-search-bar__box">
					<i class="weui-icon-search"></i>
					<input
						type="search"
						v-model="inputText"
						class="weui-search-bar__input"
						id="searchInput"
						placeholder="搜索"
						required
						ref="searchInput"
						v-on:keyup.enter="search"
					>
					<a href="javascript:" class="weui-icon-clear" id="searchClear" @click="inputText=''"></a>
				</div>
				<label class="weui-search-bar__label" id="searchText" ref="searchText" @click="searchTextClick">
					<i class="weui-icon-search"></i>
					<span>搜索</span>
				</label>
			</form>
			<a href="javascript:" class="weui-search-bar__cancel-btn" id="searchCancel" @click="isTyping=false">取消</a>
		</div>
		<div class="items">
			<div
				class="item"
				v-for="(item,index) in hotwords"
				:key="index"
				@click="itemClick(item)"
			>{{ item.dspwd }}</div>
		</div>
		<list :dataList="vodrows.length > 0 ? vodrows : hotrows"></list>
		<infinite-loading v-if="isSearch" @infinite="infinite" ref="infiniteLoading"></infinite-loading>
	</div>
</template>

<script>
import config from "../js/config.js";
import list from "../components/list.vue";

export default {
  components: {
    list
  },
  data() {
    return {
      hotwords: [],
      hotrows: [],
      vodrows: [],
      page: 1,
      inputText: "",
	  isTyping: false,
	  isSearch: false
    };
  },
  computed: {},
  created() {},
  mounted() {
	this.loadData(1);
  },
  methods: {
    searchTextClick() {
	  this.isTyping = true;
	  this.$refs.searchInput.focus()
    },
    search(res) {
      console.log("search", res);
      this.loadData(1);
    },
    itemClick(item) {
	  this.inputText = item.dspwd;
	  this.isTyping = true
      this.loadData(1);
    },
    infinite($state) {
      console.log("onReachBottom");
      this.loadData(this.page + 1, $state);
    },
    loadData(page, $state) {
      console.log("config", config, config.query());
      let query = config.query();
      query.page = page;
      if (this.inputText) {
		query.wd = this.inputText;
		this.isSearch = false
		this.$nextTick(()=>{
			this.isSearch = true
		})
      }
      this.$axios
        .request({
          url: config.origin + "/search",
          params: config.query()
        })
        .then(res => {
          if ($state) {
            $state.loaded();
          }
          console.log("success", res);
          if (res.status == 200 && res.data.retcode == 0) {
            this.page = page;
            if (res.data.data.hotwords) {
              this.hotwords = res.data.data.hotwords;
            }
            if (res.data.data.hotrows) {
              this.hotrows = res.data.data.hotrows;
            } else {
              if (this.page == 1) {
                this.vodrows = res.data.data.vodrows;
              } else {
                this.vodrows = this.vodrows.concat(res.data.data.vodrows);
              }
              if (res.data.data.vodrows.length == 0) {
                weui.topTips("没了");
                $state && $state.complete();
              }
            }
          } else {
            weui.topTips(res.data.errmsg || res.statusCode || "fail");
          }
        })
        .catch(res => {
          if ($state) {
            $state.loaded();
          }
          weui.topTips(res.message || "fail");
        });
    }
  },
  watch: {
    inputText: {
      handler: function(val, oldVal) {
      },
      immediate: false
    }
  }
};
</script>

