<template>
  <div class="list">
    <div class="header" @click="headerClick" v-if="title">
      <span class="title">{{title}}</span>
      <span class="more">更多</span>
    </div>
    <div class="items">
      <div
        class="item"
        :class="{itemMin:isMin}"
        v-for="(item, index) in dataList"
        :key="index + item.vodid"
        @click="itemClick(item, index)"
      >
        <video
          class="video"
          :class="{videoMin:isMin}"
          controls="controls"
          playsinline
          v-if="vodid == item.vodid && videoUrl"
          autoplay
          :title="item.title"
          :poster="item.coverpic"
          :picture-in-picture-mode="['push', 'pop' ]"
          :src="videoUrl"
        ></video>
        <!-- <img class="image" :class="{imagemMin:isMin}" v-else :src="item.coverpic" mode="aspectFit"> -->
        <img
          class="image"
          :class="{imagemMin:isMin}"
          v-else
          v-lazy="item.coverpic"
          mode="aspectFit"
        >
        <span>{{item.title || item.catename}}</span>
        <div class="tips">
          <span class="isvip" v-if="item.isvip == 1">{{item.isvip == 1 ? 'vip' : ''}}</span>
          <span class="definition" v-if="item.definition>0">{{item.definition}}</span>
          <span class="updatetime" v-if="!isMin">{{item.updatetime}}</span>
          <span class="upnum">{{item.upnum}}</span>
          <span class="scorenum">{{item.scorenum}}</span>
          <span class="duration">{{item.duration}}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import config from "../js/config.js";
export default {
  props: {
    dataList: {
      default: () => {
        return [];
      }
    },
    info: {
      default: () => {
        return {};
      }
    },
    title: {
      default: ""
    },
    isMin: {
      default: false
    }
  },
  data() {
    return {
      vodid: "",
      videoUrl: ""
    };
  },
  methods: {
    headerClick() {
      if (this.info.spid) {
        this.$router.push({
          path: "./detail?spid=" + this.info.spid
        });
      } else {
        this.$router.push({
          path: "./class"
        });
      }
    },
    itemClick(item) {
      if (item.vodid != this.vodid) {
        this.$axios({
          url: config.origin + item.play_url,
          params: config.query()
        })
          .then(res => {
            console.log("success", res);
            if (res.data.data.httpurl) {
              this.videoUrl = res.data.data.httpurl;
              this.vodid = item.vodid;
            } else if (res.data.data.httpurl_preview) {
              this.videoUrl = res.data.data.httpurl_preview;
              this.vodid = item.vodid;
            } else {
              weui.topTips(res.data.errmsg || "fail");
            }
            if (document) {
              document.title = item.title;
            }
          })
          .catch(res => {
            weui.topTips(res.message || "fail");
          });
      }
    }
  }
};
</script>

