.list {
  margin: 10px 0;

  .header {
    display: flex;
    justify-content: space-between;
    margin: 3px 10px;
  }

  .items {
    display: flex;
    padding: 5px;
    flex-wrap: wrap;
    justify-content: center;

    .item {
      margin: 5px;
      margin-top: 5px;
      font-size: 14px;
      width: 320px;
      position: relative;
      box-shadow: rgb(170, 170, 170) 0px 0px 14px 0px;
      &.itemMin {
        width: 162px;
      }
      .image {
        width: 100%;
        height: 180px;
        &.imagemMin {
          height: 288px;
        }
      }

      .video {
        width: 100%;
        height: 180px;
        &.videoMin {
          height: 288px;
        }
      }

      .tips {
        padding: 5px;
        background-color: black;
        color: white;
        opacity: 0.5;
        position: absolute;
        top: 0;
        right: 0;
      }

      .definition {
        margin: 0 5px;
      }

      .isvip {
        margin: 0 5px;
      }

      .scorenum {
        margin: 0 5px;
      }

      .duration {
        margin: 0 5px;
      }
    }
  }
}
</style>
